<template>
  <el-form
    class="ling-camera-distance-compensation"
    label-position="right"
    label-width="100px"
    :model="distanceCompensation"
    v-loading="loading"
    ref="compensationForm">
    <p class="tips">根据实际安装环境，目标测试的位置与摄像头的水平距离，设置该值可以获得更准确的温度。</p>
    <el-form-item label="补偿参数：">
      <el-input type="number" v-model="distanceCompensation.distance" placeholder="请输入摄像头距离补偿参数">
        <i slot="suffix">米</i>
      </el-input>
    </el-form-item>

    <el-form-item>
      <el-button type="primary" @click="handleSave('compensationForm')">保存</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import getSettingInfo from '@/mixins/getSettingInfo'
import { mapActions } from 'vuex'
export default {
  name: 'LingCameraDistanceCompensation',
  mixins: [getSettingInfo],
  data () {
    return {
      distanceCompensation: {
        distance: ''
      }
    }
  },
  watch: {
    getSettingInfo: {
      handler (val) {
        this.distanceCompensation.distance = +val.distance
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    ...mapActions({
      setTemperDistance: 'setTemperDistance'
    }),
    handleSave (formName) {
      if (this.loading) {
        return
      }

      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          this.loading = true
          try {
            await this.setTemperDistance(this.distanceCompensation)
          } catch (res) {}

          this.loading = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>

<style lang="scss">
.ling-camera-distance-compensation {

}
</style>



